import * as Actions from '../actions'

const initialState = {
    data: null,
    productList: [],
    filters: null,
    category_seo: null,
    categories: null
}

const categoryReducer = (state = initialState, action) => {
    switch(action.type)
    {
        case Actions.GET_CATEGORY_PRODUCTS:
            {
                return {
                    ...state,
                    productList: action.payload.data,
                    filters: action.payload.filters,
                    categories: action.payload.categories,
                    cat_tags: action.payload.cat_tags,
                    selectedFilter: action.payload.selectedFilter
                }
            }

        case Actions.GET_CATEGORY_PRODUCTS_BY_FILTER:
            {
                return {
                    ...state,
                    productList: action.payload.data,
                    filters: action.payload.filters,
                    categories: action.payload.categories,
                    cat_tags: action.payload.cat_tags,
                    selectedFilter: action.payload.selectedFilter
                }
            }
        
        case Actions.GET_CATEGORY_SEO:
            {
                return {
                    ...state,
                    category_seo: action.payload
                }
            }

        case Actions.GET_SORTED_PRODUCTS:
            {
                return {
                    ...state,
                    productList: action.payload
                }
            }

        case Actions.GET_SELECTED_FILTERS:
            {
                return {
                    ...state,
                    selectedFilter: action.payload
                }
            }

        default:
            return state;
    }
}

export default categoryReducer