import * as Actions from '../actions'

const initialState = {
    data: null 
}

const logoReducer = (state = initialState, action) => {
    switch(action.type)
    {
        case Actions.GET_LOGOS:
            {
                return {
                    ...state,
                    logoData: action.payload.logoData,
                    slidesToShow: action.payload.slidesToShow
                }
            }
        default:
            return state;
    }
}

export default logoReducer;