import * as Actions from "../actions";

const initialState = {
  data: null,
  product_seo: null,
  product_review: null,
  avgRating: null,
  totalRating: null,
  totalReview: null,
  test: 0,
};

const productReducer = (state = initialState, action) => {
  switch (action.type) {
    case Actions.GET_PRODUCT: {
      return {
        ...state,
        product_detail: action.payload,
      };
    }

    case Actions.GET_CATEGORY_FOR_PRODUCT: {
      return {
        ...state,
        category_of_product: action.payload,
      };
    }

    case Actions.GET_PRODUCT_REVIEW:
      console.log(action.payload);
      {
        return {
          ...state,
          product_review: action.payload.data,
          avgRating: action.payload.avgRating,
          totalRating: action.payload.totalRating,
          totalReview: action.payload.totalReview,
        };
      }

    case Actions.GET_SORTED_REVIEWS: {
      return {
        ...state,
        product_review: action.payload,
      };
    }

    case Actions.ADD_PRODUCT_REVIEW: {
      return {
        ...state,
      };
    }

    case Actions.GET_PRODUCT_SEO: {
      return {
        ...state,
        product_seo: action.payload,
      };
    }

    case Actions.GET_SIMILAR_PRODUCT: {
      return {
        ...state,
        similar_products: action.payload,
      };
    }

    default:
      return state;
  }
};

export default productReducer;

