import * as Actions from '../actions'

const initialState = {
    data: null,
    order_history: [],
    order_re_order: [],
    order_detail: {}
}

const accountReducer = (state = initialState, action) => {
    switch(action.type)
    {
        case Actions.GET_PROFILE:
            {
                return {
                    ...state,
                    userProfile: action.payload,
                    Pswdmsg: '',
                    Addressmsg:'',
                    Contactmsg:'',
                    Profilemsg:'',
                    Childmsg:''
                }
            }

        case Actions.GET_COUPONS:
            {
                return {
                    ...state,
                    cash_coupons: action.payload
                }
            }

        case Actions.UPDATE_PROFILE:
            {
                return {
                    ...state,
                    Profilemsg: action.payload.Profilemsg,
                    variant: action.payload.variant
                }
            }

        case Actions.UPDATE_CONTACT:
            {
                return {
                    ...state,
                    Contactmsg: action.payload.Contactmsg,
                    variant: action.payload.variant
                }
            }

        case Actions.RESEND_OTP:
            {
                return {
                    ...state,
                }
            }

        case Actions.UPDATE_ADDRESS:
            {
                return {
                    ...state,
                    Addressmsg: action.payload.Addressmsg,
                    variant: action.payload.variant
                }
            }

        case Actions.UPDATE_PASSWORD:
            {
                return {
                    ...state,
                    Pswdmsg: action.payload.Pswdmsg,
                    variant: action.payload.variant
                }
            }

        case Actions.UPDATE_PASSWORD_ERROR:
            {
                return {
                    ...state,
                    Pswdmsg: action.payload.Pswdmsg,
                    variant: action.payload.variant
                }
            }

        case Actions.ADD_CHILD:
            {
                return {
                    ...state,
                    Childmsg: action.payload.msg,
                    variant: action.payload.variant
                }
            }

        case Actions.UPDATE_CHILD:
            {
                return {
                    ...state,
                    Childmsg: action.payload.msg,
                    variant: action.payload.variant
                }
            }

        case Actions.DELETE_CHILD:
            {
                return {
                    ...state,
                    Childmsg: action.payload.msg,
                    variant: action.payload.variant
                }
            }
        
        case Actions.UPDATE_EMAIL_MARKET:
            {
                return {
                    ...state
                }
            }

        case Actions.UPDATE_SMS_MARKET:
            {
                return {
                    ...state
                }
            }

        default:
            return state
    }
}

export default accountReducer;