export function getCategoryProducts(slug, id, filterquery)
{
    console.log('Filter Query in action :: ' + JSON.stringify(filterquery));
    // let filterquery = getFilterQuery(selectedFilter, serverFilters);

    console.log('Filtered Query :: ' + filterquery)

    let query = `${hostUrl}/api/customer/categories/getProductsForCategory/${id}?slug=${slug}&${filterquery}`;

    let request = fetch(query)

    return (dispatch) => {
        request
        .then(response => response.json())
        .then((data) => {
            if(data.status === true)
            {
                console.log('Products Data :: ' + JSON.stringify(data.data));

                let cat_data = []
                let Tags = []

                  if(data.categories.length > 0)
                  {
                    let alltags = [] 
                    
                    cat_data = _.map(data.data, item =>
                        _.pick(item, [
                          "child_id",
                          "child_name",
                          "child_slug",
                          "AllTags",
                          "subCategories"
                        ])
                      );

                    _.map(data.categories, category => 
                        category['AllTags'].map(tag => {
                            alltags.push(tag.tag)
                        })
                    )
                    Tags = _.uniq(alltags)
                  }
                
                dispatch({
                    type: GET_CATEGORY_PRODUCTS,
                    payload: {
                            data : data.data,
                            categories: data.categories,
                            cat_tags: Tags,
                            filters : data.filters,
                        }
                    })
                // dispatch(populateServerFilters(data.filters,filterquery))
            }
            else
            {
                console.log('Error :: ' + data.msg);
            }
        })
        .catch(err => {
            console.log('Server Error :: ' + err);
        })
    }
}

export function getCategorySEO(slug)
{
    let url = `${hostUrl}/api/customer/categories/category_seo/${slug}`

    return dispatch => {
        fetch(url)
        .then(response => response.json())
        .then(result => {
            if(result.status === true)
            {
                dispatch({
                    type: GET_CATEGORY_SEO,
                    payload: result.data
                })
            }
        })
    }
}

