export function getFilterCollection(selectedFilter, filter)
{
    return (dispatch) => {
        filterCollection(selectedFilter, filter)
        .then((collection) => {

            dispatch({
                type: GET_SELECTED_FILTERS,
                payload: collection
            })
        })
    }
}

export function getPopulateSelectedFilters(serverFilter)
{

    return new Promise((resolve, reject) => {
        let selectedFilter = [];
        serverFilter.map((filter) => {
            let obj = {
                name : filter.AttCategory,
                value : filter.Filters
            }
    
            selectedFilter.push(obj)
        })
        resolve(selectedFilter);
    })

    // return (dispatch) => {
    //     dispatch({
    //         type: GET_CATEGORY_FILTERS,
    //         payload: selectedFilter
    //     })
    // }

}

export function handleSortProduct(sortType, products)
{
    console.log('Sort Type :: ' + sortType)
    return dispatch => {
        if(products && Object.keys(products).length > 0)
        {
            let pro = []
            switch(sortType)
            {
                case 'name':
                    pro = _.sortBy(products, ['Name']);
                    break;

                case 'price':
                    pro = _.sortBy(products, (pro) => {
                        if(pro['Type'] === 'simple')
                        {
                            return pro.Price
                        }
                        else
                        {
                            return Object.keys(pro['Varients']).length > 0 && pro['Varients'][0]['Price']
                        }
                    });
                    break;

                case 'discount':
                    pro = _.orderBy(products, (pro) => {
                        if(pro['Type'] === 'simple')
                        {
                            if(pro['Sale Price'] > 0)
                            {
                                // return pro['Sale Price']
                                return Math.round(((pro['Price'] - pro['Sale Price']) / pro['Price'] * 100))
                            }
                        }
                        else
                        {
                            if(Object.keys(pro['Varients']).length > 0 && pro['Varients'][0]['Sale Price'] > 0)
                            {
                                // return pro['Varients'][0]['Sale Price']
                                return Math.round(((pro['Varients'][0]['Price'] - pro['Varients'][0]['Sale Price']) / pro['Varients'][0]['Price'] * 100))
                            }
                        }
                    }, ['asc'])
                    break;

                case 'new-arrivals':
                    pro = _.orderBy(products, ['Id'], ['desc'])
                    break;

                default:
                    pro = products
            }

            dispatch({
                type: GET_SORTED_PRODUCTS,
                payload: pro
            })
        }
    }
}