import * as Actions from '../actions'

const initialState = {
    response_message: '',
    variant: '',
}

const notificationReducer = (state = initialState, action) => {
    switch(action.type)
    {
        case Actions.SHOW_RESPONSE_MESSAGE:
            {
                return {
                    ...state,
                    response_message: action.payload.response_message,
                    variant: action.payload.variant
                }
            }

        case Actions.HIDE_RESPONSE_MESSAGE:
            {
                return {
                    ...state,
                    response_message: action.payload.response_message,
                    variant: action.payload.variant
                }
            }

        case Actions.SHOW_CART_RESPONSE_MESSAGE:
            {
                return {
                    ...state,
                    cart_response_message: action.payload.cart_response_message,
                    cart_variant: action.payload.cart_variant
                }
            }

        case Actions.HIDE_CART_RESPONSE_MESSAGE:
            {
                return {
                    ...state,
                    cart_response_message: action.payload.cart_response_message,
                    cart_variant: action.payload.cart_variant
                }
            }

        case Actions.SHOW_AUTH_RESPONSE_MESSAGE:
            {
                return {
                    ...state,
                    auth_response_message: action.payload.auth_response_message,
                    auth_variant: action.payload.auth_variant
                }
            }

        case Actions.HIDE_AUTH_RESPONSE_MESSAGE:
            {
                return {
                    ...state,
                    auth_response_message: action.payload.auth_response_message,
                    auth_variant: action.payload.auth_variant
                }
            }

        case Actions.SHOW_SIGNUP_RESPONSE_MESSAGE:
            {
                return {
                    ...state,
                    signup_response_message: action.payload.auth_response_message,
                    signup_variant: action.payload.auth_variant
                }
            }

        case Actions.HIDE_SIGNUP_RESPONSE_MESSAGE:
            {
                return {
                    ...state,
                    signup_response_message: action.payload.auth_response_message,
                    signup_variant: action.payload.auth_variant
                }
            }

        default:
        return state
    }
}

export default notificationReducer;