import {hostUrl} from '../config';

// ... CONSTANTS ...

export const GET_BANNER = "[BANNER] GET BANNER";

// ... ACTIONS ...

export function getBanner()
{
    // console.log('Recieved ...');
    let query = `${hostUrl}/api/customer/misc/home-banner`;
    let request = fetch(query);
    
    return (dispatch) => {
        request
        .then(response =>  response.json())
        .then((data) => {
            if(data.status === true)
            {
                // console.log('Success  :: ' + data.msg);
                dispatch({
                    type: GET_BANNER,
                    payload: data.data
                })                
            }
            else {
                // console.log('Error  :: ' + data.msg);
            }
        })
        .catch(err => {
            // console.log('Server Error  :: ' + err);            
        })
    }
}