import * as Actions from '../actions'

const initialState = {
    data: null,
    home_seo: null
}

const headerReducer = (state = initialState, action) => {
    switch(action.type)
    {
        case Actions.RE_RENDER:
            {
                return {
                    ...state,
                    reRender: action.payload.reRender
                }
            }

        case Actions.GET_HEADER_CATEGORIES:
            {
                return {
                    ...state,
                    categoriesData: action.payload.categoryData,
                    categoriesTags: action.payload.categoriesTags,
                    // attributes: action.payload.attributes
                }
            }

        case Actions.GET_HOME_SEO:
            {
                return {
                    ...state,
                    home_seo: action.payload
                }
            }

        default:
            return state;
    }
}

export default headerReducer;