import {
    productFoundInCache,
    productInCacheDetail, 
    productFoundInShortlist,
    getLocalStorageObject,
    removeProductFromshortlist
} from '../../Utilities'

import Cookies from 'universal-cookie';
import _ from 'lodash'

// ... CONSTANTS ...
export const SHORTLIST_UPDATE = "[SHORTLIST] SHORTLIST UPDATE";
export const SHORTLIST_SAVE = '[SHORTLIST] SHORTLIST SAVE';
export const SHORTLIST_REMOVE = '[SHORTLIST] SHORTLIST REMOVE';

// ... ACTIONS ... 

export function saveProductToShortlist(product)
{
    let shortlist = getLocalStorageObject('shortlist');

    return (dispatch) => {
        if (!(productFoundInShortlist(product.Id, shortlist)))
        {
            shortlist.push(product);
            localStorage.setItem('shortlist',JSON.stringify(shortlist));
            dispatch({
                type:SHORTLIST_SAVE,
                payload: {trigger:true, st_ID:product.Id}
            })
        }
        else
        {
            console.log('Hai bhai is me');
            removeProductFromshortlist(product && product.Id);
            dispatch({
                type:SHORTLIST_REMOVE,
                payload: {trigger:false, st_ID:product.Id}
            })
        }   

    } 
}