import * as Actions from '../actions'

const initialState = {
    data : null
}

const searchReducer = (state = initialState, action) => {
    switch(action.type)
    {
        case Actions.SEARCH:
            {
                return {
                    ...state,
                    searchData: action.payload 
                }
            }

        default:
            return state
    }
}

export default searchReducer