import { hostUrl } from "../config";
import jwtService from "../../../services/jwtService";
import jwtDecode from 'jwt-decode'

import history from '../../../history'

import {showResponseMessage, showAuthResponseMessage, showSignupResponseMessage} from './notification.actions'
import {updateContact} from './account.actions'

// ... CONSTANTS ...

export const GET_REGISTRATION_SOCIAL = "[AUTH] GET REGISTRATION SOCIAL";
export const GET_LOGIN_SOCIAL = "[AUTH] GET LOGIN SOCIAL";
export const GET_MANUAL_LOGIN = "[AUTH] GET MANUAL LOGIN";
export const SIGNUP = "[AUTH] GET SIGNUP";
export const LOGOUT = "[AUTH] LOGOUT";
export const LOGIN_SUCCESS = "[AUTH] LOGIN SUCCESS";
export const LOGIN_ERROR = "[AUTH] LOGIN ERROR";
export const SET_USER_DATA = "[AUTH] SET USER DATA";
export const REMOVE_USER_DATA = "[AUTH] REMOVE USER DATA";
export const GET_CART_FOR_CUSTOMER = "[AUTH CART] GET CART FOR AUTH CUSTOMER";
export const GET_RESEND_EMAIL = '[AUTH] GET RESEND EMAIL'
export const GUEST_SIGNUP = '[AUTH] GUEST SIGNUP'

export const VERIFY_OTP = '[AUTH] VERIFY OTP'
export const SHOW_OTP = '[AUTH] SHOW OTP'
export const CLOSE_OTP= '[AUTH] CLOSE OTP'

export const STATE_NULL = '[AUTH STATE MSG] STATE NULL'

// ... ACTIONS ...

export function SocialRegistration({ name, email, id }, source, signup_with) {

  return (dispatch) => {
    jwtService
      .signInWithSocialLogin({ name, email, id }, source, signup_with)
      .then((data) => {
        dispatch(setUserData(data.data));
        history.go();
        return dispatch({
          type: GET_REGISTRATION_SOCIAL,
          payload: {
          isAuthenticated: true,
          user_object: { name, email, user_id: id },
          msg: data.msg
          }
        })
      })
      .catch(error => {
        console.log("Rejected... :: " + error);
        return dispatch({
          type: LOGIN_ERROR,
          payload: error
        });
      });
  }
}

export function SocialLogin(id) {
  let endpoint = `${hostUrl}/api/customer/login/social`;

  let formBody = new FormData();
  formBody.append("user_id", id);

  let request = fetch(endpoint, {
    method: "POST",
    body: formBody
  });

  return dispatch => {
    request
      .then(response => response.json())
      .then(data => {
        if (data.status === true) {
          dispatch(setUserData(data.data));
          history.go();
          return dispatch({
            type: GET_LOGIN_SOCIAL,
            payload: {
              isAuthenticated: true,
              user_object: data.data,
              login_msg: data.msg
            }
          });
        } else {
          return dispatch({
            type: GET_LOGIN_SOCIAL,
            payload: {
              isAuthenticated: false,
              user_object: null,
              login_msg: data.msg
            }
          });
        }
      });
  };
}

export function manualLogin(email, password, isRemember = false) {
  return dispatch => {
    jwtService
      .signInWithEmailAndPassword(email, password, isRemember)
      .then(user => {
        
        dispatch(setUserData(user.data));
        
        dispatch(showAuthResponseMessage(user.msg, 'success'))

        history.go();
        return dispatch({
          type: LOGIN_SUCCESS,
          payload: { user: user.data, login_msg: user.msg, variant: 'green', is_verified: null}
        });
      })
      .catch(error => {
        console.log("Rejected... :: " + error.msg);
      
        if(error.data && error.data.is_verified === 0)
        {
          // we need to active the resend option...
          // alert('got the alertsss')
          dispatch(showAuthResponseMessage(error.msg, 'danger'))
          return dispatch({
            type: LOGIN_ERROR,
            payload: {login_msg: error.msg, variant: 'red', is_verified: error.data.is_verified}
          });
        }
        else
        {
          dispatch(showAuthResponseMessage(error.msg, 'danger'))
          return dispatch({
            type: LOGIN_ERROR,
            payload: {login_msg: error.msg, variant: 'red', is_verified: null}
          });
        }
        
      });
  };
}

export function SignUp(dataArray) {
  let query = `${hostUrl}/api/customer/signup`;

  let request = fetch(query, {
    method: "POST",
    body: dataArray
  });

  return dispatch => {
    request
      .then(response => response.json())
      .then(data => {
        if (data.status === true) {
          // console.log('Success :: ' + data.msg);
          if(data.token)
          {
            dispatch(showSignupResponseMessage('Provided email or phone already exists, Please login','danger'))
            return dispatch({
              type: SIGNUP,
              payload: {signup_msg:'Provided email or phone already exists, Please login', variant:'red'}
            });
          }
          else
          {
            // history.go()
            dispatch(showSignupResponseMessage(data.msg, 'success'))
            return dispatch({
              type: SIGNUP,
              payload: {signup_msg:data.msg, variant:'green'}
            });
          }
        } else {
          dispatch(showSignupResponseMessage(data.msg, 'danger'))
          return dispatch({
            type: SIGNUP,
            payload: {signup_msg:data.msg, variant:'red'}
          });
        }
      });
  };
}

export function guestSignup(dataArray, flag = -1) {

  flag > -1 ?
  console.log('I am signing up without OTP')
  :
  console.log('I am signing up with OTP')


  let url = flag > -1 ?
  `${hostUrl}/api/customer/signup/guest?flag=1`
  :
  `${hostUrl}/api/customer/signup/guest`

  let request = fetch(url, {
    method: 'POST',
    body: dataArray
  })

  return dispatch => {
    request
    .then(response => response.json())
    .then(data => {
      if(data.status === true)
      {
        if(data.token)
        {
          // alert(JSON.stringify(data.data));
          jwtService.setGuestSession(data)
          .then(() => {
            dispatch({
              type: VERIFY_OTP,
              payload: {
                guest_obj: data.data,
                msg: '',
                variant: '',
              }
            })
            dispatch(showResponseMessage(data.msg,'success'))
          })
        }
        else
        {
          dispatch(showResponseMessage(data.msg, 'success'))
          dispatch(showOTP())
          dispatch({
            type: GUEST_SIGNUP,
            payload: {
              guest_variant: 'green',
              guest_msg: data.msg
            }
          })
        }
      }
      else
      {
        dispatch(showResponseMessage(data.msg, 'danger'))
        dispatch({
          type: GUEST_SIGNUP,
          payload: {
            guest_variant: 'red',
            guest_msg: data.msg
          }
        })
      }
    })
    .catch(err => {
      // alert('Err :: ' + JSON.stringify(err))
      dispatch(showResponseMessage(err, 'danger'))
      dispatch({
        type: GUEST_SIGNUP,
        payload: {
          guest_variant: 'red',
          guest_msg: err.msg
        }
      })
    })
  }
}

export function verifyOTP(code, phone, location = ``) {
  let url = `${hostUrl}/api/customer/signup/otp_verification`

  let formBody = new FormData()
  formBody.append('code', code)
  formBody.append('number', phone)

  let request = fetch(url, {
    method: 'POST',
    body: formBody
  })

  return dispatch => {
    request
    .then(response => response.json())
    .then(data => {
      if(data.status === true)
      {
        if(data.token)
        {
          dispatch(closeOTP())
          if(location === '/checkout')
          {
            // alert('Its Guest')
            // alert(JSON.stringify(data))
            jwtService.setGuestSession(data)
            .then(() => {
              dispatch({
                type: VERIFY_OTP,
                payload: {
                  guest_obj: data.data,
                  msg: '',
                  variant: '',
                }
              })
              dispatch(showResponseMessage(data.msg,'success'))
            })
          }
          else
          {
            // alert('Its General')
            // dispatch(closeOTP())
            let formBody2 = new FormData();
            if(localStorage.getItem('jwtToken'))
            {
                let cust_id = (jwtDecode(localStorage.getItem('jwtToken'))).cust_id;
                formBody2.append('cust_id',cust_id)
                formBody2.append('mobile', phone);
                
                dispatch({
                  type: VERIFY_OTP,
                  payload: {
                    guest_obj: null,
                    msg: '',
                    variant: '',
                  }
                })
                dispatch(updateContact(formBody2))
            }
          }
        }
      }
      else
      {
        // dispatch(closeOTP())
        dispatch({
          type: VERIFY_OTP,
          payload: {
            msg: data.msg,
            variant: 'red'
          }
        })
        // dispatch(showResponseMessage(data.msg,'success'))
      }
    })
  }
}

export function getResendEmail(email) 
{
  let URL = `${hostUrl}/api/customer/signup/resend-email/${email}`

  return dispatch => {
    fetch(URL)
    .then(response => response.json())
    .then(data => {
      if(data.status === true)
      {
        dispatch(showAuthResponseMessage(data.msg,'success'))
        return dispatch({
          type: GET_RESEND_EMAIL,
          payload: null
        })
      }
      else
      {
        dispatch(showAuthResponseMessage(data.msg,'danger'))
        return dispatch({
          type: GET_RESEND_EMAIL,
          payload: null
        })
      }
    })
    .catch(err => {
      dispatch(showAuthResponseMessage(err,'danger'))
      return dispatch({
        type: GET_RESEND_EMAIL,
        payload: null
      })
    })

  }
}

export function stateNull()
{
  return (dispatch) => {
    dispatch({
      type: STATE_NULL,
      payload: {login_msg: ''}
    })
  }
}

export function Logout() {
  // console.log('Logout Triggered');
  return dispatch => {
    jwtService.logout().then(() => {
      return dispatch({
        type: LOGOUT,
        payload: { reload: Math.random() }
      });
    });
  };
}

export function getCartForCust(cust_id)
{
    console.log ("GetCartFromCustomer");
  let query = `${hostUrl}/api/customer/cart/${cust_id}`;
  
  let request = fetch(query);

  return dispatch => {
    request
      .then(response => response.json())
      .then(data => {
        if (data.status === true) {
        console.log('Cache Cart :: ' + JSON.stringify(data.data.cacheFromServer));
        localStorage.setItem ('procache' , JSON.stringify(data.data.cacheFromServer));
          dispatch({
            type: GET_CART_FOR_CUSTOMER,
            payload: {
              productData: data.data.productData, 
              productQuanities: data.data.productQuantities, 
              itemCount: Object.keys(data.data.productData).length, 
            }
          });
        } else {
            return (console.log(data.msg))
        }
      });
  };
}

/**
 * Set User Data
 */
export function setUserData(user) {
  return dispatch => {
    dispatch({
      type: SET_USER_DATA,
      payload: user
    });
  };
}

export function removeUserData(user) {
  return dispatch => {
    dispatch({
      type: REMOVE_USER_DATA,
      payload: { user: null }
    });
  };
}


export function showOTP() {
  return dispatch => 
    dispatch({
      type: SHOW_OTP,
      payload: {
        guest_variant: '',
        guest_msg: '',
      }
    })
}

export function closeOTP() {
  return dispatch => 
    dispatch({
      type: SHOW_OTP,
      payload: false
    })
}