import * as Actions from '../actions'
import {getLocalStorageObject} from '../../Utilities'

const initialState = {
    itemCount: Object.keys(getLocalStorageObject('procache')).length,
    cart_summary: []
}

const cartReducer = (state = initialState, action) => {
    switch(action.type)
    {
        case Actions.CART_UPDATE:
            {
                return {
                    ...state,
                    itemCount: action.payload.itemCount,
                    cartItems: action.payload.cartItems,
                    procache: action.payload.procache
                }
            }

        case Actions.CART_SAVE:
            {
                return {
                    ...state,
                    itemCount: action.payload.itemCount,
                    cartItems: action.payload.cartItems,
                    procache: action.payload.procache
                }
            }

        case Actions.GET_MINI_CART:
            {
                return {
                    ...state,
                    itemCount: action.payload.itemCount,
                    cartItems: action.payload.cartItems,
                    procache: action.payload.procache
                }
            }

        case Actions.REMOVE_CART:
            {
                return {
                    ...state,
                    itemCount: action.payload.itemCount,
                    cartItems: action.payload.cartItems,
                    procache: action.payload.procache
                }
            }

        case Actions.CART_SUMMARY:
            {
                return {
                    ...state,
                    cart_summary: action.payload.summary,
                    procache: action.payload.procache,
                    data: action.payload.data
                }
            }

        default:
            return state
    }
}

export default cartReducer;