
import { hostUrl } from '../config'
import _ from 'lodash'

// ... CONSTANTS ...
export const RE_RENDER = "[HEADER] RE RENDER";
export const GET_HEADER_CATEGORIES = '[HEADER CATEGORIES] GET CATEGORIES';
export const GET_HOME_SEO = '[HOME] GET HOME SEO'


// ... ACTIONS ..
export function renderAction()
{
 return (dispatch) => {
     return dispatch({
         type: RE_RENDER,
         payload: {reRender:2}
     })
 }   
}

export function getHomeSEO()
{
    let url = `${hostUrl}/api/customer/home/`

    return dispatch => {
        fetch(url)
        .then(response => response.json())
        .then(result => {
            if(result.status === true)
            {
                // console.log('Home SEO :: ' + JSON.stringify(result.data))
                dispatch({
                    type: GET_HOME_SEO,
                    payload: result.data
                })
            }
        })
    }
}

export function getCategories()
{
    let query = `${hostUrl}/api/customer/categories/`
    let request = fetch(query)

    return (dispatch) => {
        request
        .then(response => response.json())
        .then((data) => {
            if(data.status === true)
            {
                // console.log('Success :: ' + data.msg);

                let cat_data = _.map(data.data, item =>
                    _.pick(item, [
                      "child_id",
                      "child_name",
                      "child_slug",
                      "AllTags",
                      "subCategories"
                    ])
                  );

                let cat_tags = _.map(data.data, item =>
                    _.pick(item, ["child_id", "sub"])
                  );

                //   console.log('Category data :: ' + JSON.stringify(cat_data));
                //   console.log('Data of categories :: ' + JSON.stringify(cat_data))
                //   console.log('Tags of Categories :: ' + JSON.stringify(cat_tags))

                return dispatch({
                    type: GET_HEADER_CATEGORIES,
                    payload: {
                        categoryData: cat_data,
                        categoriesTags: cat_tags,
                        // attributes: data.data.attributes
                    }
                })
            }
            else
            {
                return;
                return (console.log('Error :: ' + data.msg))
            }
        })
        .catch(err => {
            return;
            return (console.log('Server Error :: ' + err))
        })
    }
}