import React, { Component } from 'react';

class Loading extends Component {


    constructor (props) {
        super(props); 
        this.state = {

        }
    }

    getStyleForProduct = () => {
        if (this.props.isProduct)
        {
            const style = {
                height: '50px', 
                width: '50px', 
                border: '8px solid #f3f3f3',
                borderTop: '8px solid #fcd72b'
            }
            return style ;
        }
        else
            return null;
    }

    render() {
        return (
            <div className="loader-style" >
                <div className="loader" style={this.getStyleForProduct()}></div>
            </div>
            // <div class="codewrapper">
            //     <div class="codeprogressbar">
            //          <div class="codestylization"></div>
            //     </div>
            // </div>
        )
    }
}

export default Loading;
