import {hostUrl} from '../config';

// ... CONSTANTS ...

export const GET_LOGOS = "[LOGOS] GET LOGOS";

// ... ACTIONS ...

export function getLogos()
{
    let query = `${hostUrl}/api/admin/misc/sponsored-logos/getAllLogos`; 
    
    let request = fetch(query)

    return (dispatch) => {
        request
        .then(response => response.json())
        .then((data) => {
            if(data.status === true)
            {
                // console.log('Success :: ' + data.msg);
                
                return dispatch({
                    type: GET_LOGOS,
                    payload: { logoData: data.data, slidesToShow: data.data.length }
                })
            }
            else {
                // console.log('Error :: ' + data.msg);
            }
        })
        .catch(err => {
            // console.log('Server Error :: ' + err);
        })
    }
}