import _ from 'lodash'


export function productFoundInCache(productID, productsCache) {
  if (productsCache && typeof productsCache === "string") {
    productsCache = eval(JSON.parse(productsCache.replace(/\'/g, '"')));
    let toRet = false;
    for (let i = 0; i < Object.keys(productsCache).length; i++) {
      if (productsCache[i][0] === productID) {
        toRet = true;
      }
    }
    return toRet;
  } else if (productsCache && typeof productsCache === "object") {
    let toRet = false;
    for (let i = 0; i < Object.keys(productsCache).length; i++) {
      if (productsCache[i][0] === productID) {
        toRet = true;
      }
    }
    return toRet;
  }
  return false;
}

