export function getLocalStorageObject(key) {
  let rawCache = localStorage.getItem(key);
  if (rawCache) {
    let cacheObject = JSON.parse(rawCache);
    return cacheObject ? cacheObject : [];
  } else return [];
}

export function getLocalStorageString(key) {
  let rawCache = localStorage.getItem(key);
  if (rawCache) {
    return rawCache;
  } else return null;
}

export function productFoundInShortlist(productID, productsShortlist) {
  if (typeof productsShortlist === "object") {
    let toRet = false;
    for (let i = 0; i < Object.keys(productsShortlist).length; i++) {
      if (productsShortlist[i]['Id'] === productID) {
        toRet = true;
      }
    }
    return toRet;
  }
  return false;
}

export function removeProductFromshortlist(productID) {
  let shortlist = getLocalStorageObject('shortlist');
  let index = -1;

  for(let i=0; i < Object.keys(shortlist).length; i++)
  {
    if(shortlist[i]['Id'] === productID) index = i;
  }

  if(index > -1)
  shortlist.splice(index,1);

  localStorage.setItem('shortlist', JSON.stringify(shortlist));
}

export function removeProductFromCache(productId) {
  let cacheObject = getLocalStorageObject("procache");

  let foundIndex = -1;
  for (let i = 0; i < Object.keys(cacheObject).length; i++) {
    if (productId === cacheObject[i][0]) foundIndex = i;
  }

  cacheObject.splice(foundIndex, 1);

  localStorage.setItem("procache", JSON.stringify(cacheObject));
}

export function removeProductDetailFromCache(productId)
{
  let cacheObject = getLocalStorageObject("cachedProDetail");

  let foundIndex = -1;
  for (let i = 0; i < Object.keys(cacheObject).length; i++) {
    if (productId === cacheObject[i]['Id']) foundIndex = i;
  }

  cacheObject.splice(foundIndex, 1);

  localStorage.setItem("cachedProDetail", JSON.stringify(cacheObject));

}
    

export function filterCollection(filterArray = [], filters, serverFilter = [])
{
  // console.log('Filter Array :: ' + JSON.stringify(filterArray))
  // console.log('Filters :: ' + JSON.stringify(filters))
  // console.log("Server Array Before :: " + JSON.stringify(serverFilter));

  console.log('Filters :: ' + JSON.stringify(filters))

  if (Array.isArray(filters)) {
    filterArray = formulateArrayOfFilter(filterArray, filters, serverFilter)
    console.log("Filter Array From Array :: " + JSON.stringify(filterArray));
  } else if (typeof filters === "object") {
    filterArray = formulateObjectOfFilter(filterArray, filters, serverFilter)
    console.log("Filter Array From Object :: " + JSON.stringify(filterArray));
  }

  // return new Promise((resolve, reject) => {
  //   resolve(filterArray)
  // })
  return filterArray
}

export function clearFilter(filterArray = [], filter, serverFilter = [])
{
  if(serverFilter && Object.keys(serverFilter).length > 0)
  {
    let attIndex = _.find(serverFilter, att => {
      return att.AttCategory === filter.name
    })

    if(attIndex)
    {
      let filterIndex = _.find(filterArray, att => {
        return att.name === filter.name;
      });

      if (filterIndex) 
      {
        filterArray[filterIndex].value = []
      }
      else
      {
        let obj = {
          name : filter.name,
          value: []
        }
        filterArray.push(obj)
      }
    }
  }

  return filterArray
}

const formulateArrayOfFilter = (filterArray, filters, serverFilter) =>
{
  // console.log('Array Filter')
  filters.map(filter => {
    if (serverFilter && Object.keys(serverFilter).length > 0) {
        if(filter.name !== 'Price')
        {
          // So we need to check filter URLs form serverFilter array as well ...
          let attIndex = isAttributeExistInServerFilter(
            filter.name,
            serverFilter
          );
          // console.log("Att Name ::" + attIndex);
          // console.log(
          //   "Server :: " + JSON.stringify(serverFilter[attIndex].Filters)
          // );
          if (attIndex >= 0) {
            let isDiff = _.difference(
              filter.value ? filter.value.split(",") : [],
              serverFilter[attIndex].Filters
            );

            let filteredValues = filter.value ? _.difference(filter.value.split(","), isDiff) : [];
            let attName = isAttributeExistInSelectedFilter(
              filter.name,
              filterArray
            );
            if (attName >= 0) {
              // Attribute Exist ...
              // Now we'll check the existance of the value of the Filter URI in that Attribute values ...
              filteredValues.map(val => {
                if (!isValueExist(val, filterArray[attName].value)) {
                  // alert('aaaa :: ' + val)
                  filterArray[attName].value.push(val);
                } else {
                  // console.log("aa gyaa :: " + val);
                  // console.log("Array :: " + JSON.stringify(filterArray[attName]));
                  filterArray[attName].value = _.dropWhile(
                    filterArray[attName].value,
                    v => {
                      return v === val;
                    }
                  );
                  // console.log(
                  //   "now Array :: " + JSON.stringify(filterArray[attName])
                  // );
                }
              });
              // filterArray[attName].value = values;
            } else {
              // Attribute Not Exist ...
              // we'll add that Attribute and its values in the selected Array...
              let obj = {
                name: filter.name,
                value: filteredValues
              };
              filterArray.push(obj);
            }
          }
        }
        else
        {
          let priceIndex = _.findIndex(filterArray, (item) => {return item.name === filter.name})

          if(priceIndex > -1)
          {
            filterArray[priceIndex].value[0].min = filter.value.min
            filterArray[priceIndex].value[0].max = filter.value.max
          }
          else
          {
            let obj = {
              name : filter.name,
              value: [filter.value]
            }

            filterArray.push(obj)
          }
        }
      } else {
        if(filter.name !== 'Price')
        {        
          // We don't need to check it from serverFilter because we don't have that array ...
          // Check whether the Attribute exist in the Selected Array or not
          let attName = isAttributeExistInSelectedFilter(
            filter.name,
            filterArray
          );
          if (attName >= 0) {
            // Attribute Exist ...
            // Now we'll check the existance of the value of the Filter URI in that Attribute values ...
            filter.value && filter.value.split(",").map(val => {
              if (!isValueExist(val, filterArray[attName].value)) {
                filterArray[attName].value.push(val);
              } else {
                // console.log("aa gyaa :: " + val);
                // console.log("Array :: " + JSON.stringify(filterArray[attName]));
                filterArray[attName].value = _.dropWhile(
                  filterArray[attName].value,
                  v => {
                    return v === val;
                  }
                );
                // console.log(
                //   "now Array :: " + JSON.stringify(filterArray[attName])
                // );
              }
            });
            // filterArray[attName].value = values;
          } else {
            // Attribute Not Exist ...
            // we'll add that Attribute and its values in the selected Array...
            let obj = {
              name: filter.name,
              value: filter.value ? filter.value.split(",") : []
            };
            filterArray.push(obj);
          }
        }
        else
        {
          let priceIndex = _.findIndex(filterArray, (item) => {return item.name === filter.name})

          if(priceIndex > -1)
          {
            filterArray[priceIndex].value[0].min = filter.value.min
            filterArray[priceIndex].value[0].max = filter.value.max
          }
          else
          {
            let obj = {
              name : filter.name,
              value: [filter.value]
            }

            filterArray.push(obj)
          }
        }
      }
  });

  // console.log("Filter Array From Array :: " + JSON.stringify(filterArray));
  return filterArray
}

const isAttributeExistInServerFilter = (attName, filterArray) => {
  return _.findIndex(filterArray, index => {
    return index.AttCategory === attName;
  });
}

const isAttributeExistInSelectedFilter = (attName, filterArray) => {
  return _.findIndex(filterArray, index => {
    return index.name === attName;
  });
}

const isValueExist = (filterValue, Values) => {
  return _.find(Values, val => {
    return val === filterValue;
  });
}

const formulateObjectOfFilter = (filterArray, filter, serverFilter) => 
{
  // console.log('Object Filter')
  if(serverFilter && Object.keys(serverFilter).length > 0)
  {
    if(filter.name !== 'Price')
    {
      let attIndex = _.find(serverFilter, att => {
        return att.AttCategory === filter.name;
      });
      
      if (attIndex) {
        let filterIndex = _.find(filterArray, att => {
          return att.name === filter.name;
        });
        // console.log("Att index :: " + JSON.stringify(attIndex));
        if (filterIndex) {
          let valIndex = _.findIndex(filterIndex.value, index => {
            return index === filter.value;
          });
          if (valIndex >= 0) {
            let values = [];
            filterIndex.value.map(val => {
              if (val !== filter.value) {
                values.push(val);
              }
            });
            filterIndex.value = values;
            // console.log(
            //   "New updated value if exist :: " + JSON.stringify(filterArray)
            // );
          } else {
            Array.isArray(filterIndex.value) ? filterIndex.value.push(filter.value) : filterIndex.value = [filter.value];
            // console.log(
            //   "Updated value if not exist :: " + JSON.stringify(filterArray)
            // );
          }
        } else {
          let value = [];
          attIndex.Filters.map(attFilter => {
            if (attFilter !== filter.value) {
              value.push(attFilter);
            }
          });
          let obj = {
            name: attIndex.AttCategory,
            value: value
          };
          filterArray.push(obj);

          // console.log("OBJ :: " + JSON.stringify(obj));
        }
      }
    }
    else
    {
      let priceIndex = _.findIndex(filterArray, (item) => {return item.name === filter.name})

      if(priceIndex > -1)
      {
        filterArray[priceIndex].value[0].min = filter.value.min
        filterArray[priceIndex].value[0].max = filter.value.max
      }
      else
      {
        let obj = {
          name : filter.name,
          value: [filter.value]
        }

        filterArray.push(obj)
      }
    }
  }
  else
  {
    let obj ={
      name: filter.name,
      value: filter.value.split(',')
    }
    filterArray.push(obj)
  }
    // console.log("Filter Array :: " + JSON.stringify(filterArray));
    // console.log("Server Array After :: " + JSON.stringify(serverFilter));
    return filterArray
}

