export function populateServerFilters(serverFilter, filterQuery)
{
    let selectedFilter = []
    console.log('query :: ' + filterQuery)
    filterQuery.split('&').map(filter => {
        let filterIndex = _.find(serverFilter, index => {return index.AttCategory && index.AttCategory.toUpperCase() === (filter.split('=')[0]).toUpperCase()})
        if(filterIndex)
        {
            // console.log('YESSSS :: ' + filter.split('=')[0].toUpperCase())
            selectedFilter.push(popFilters(filterIndex, filter))
        }
    })

    console.log('Server Filter Clone :: ' + JSON.stringify(selectedFilter));
    return selectedFilter
    // return (dispatch) => {
    //     dispatch({
    //         type: GET_POPULATED_FILTER,
    //         payload: selectedFilter
    //     })
    // }
}

function popFilters(filterArray, filter)
{
    let obj = {}
    if(filter.split('=')[0] !== 'price' &&  filter.split('=')[1] === 'all')
    {
        obj = {
            name : filterArray.AttCategory,
            value : filterArray.Filters
        }
    }
    else if(filter.split('=')[0] !== 'price' && filter.split('=')[1] !== 'null')
    {       
        // console.log('Not All and not Null')
        let attValue = []
        filter.split('=')[1].split(',').map(val => {
            if(_.find(filterArray.Filters, found => {return found === val}))
            {
                // console.log('Value found...')
                attValue.push(val)
            }
            return attValue
        })

        obj = {
            name : filterArray.AttCategory,
            value : attValue
        }
    }
    return obj
}

export function getFilterQuery(filterData = [], serverFilter = [], tag = '', p_cat = '', age = '', color ='', size = '', gender = '', price = '')
{
    let Age = getAgeValues(_.find(filterData, (data) => {return data.name === 'Age'}), _.find(serverFilter, (data) => {return data.AttCategory === 'Age'}), age)
    
    let Size = getSizeValues(_.find(filterData, (data) => {return data.name === 'Size'}), _.find(serverFilter, (data) => {return data.AttCategory === 'Size'}), size)
    
    let Color = getColorValues(_.find(filterData, (data) => {return data.name === 'Color'}), _.find(serverFilter, (data) => {return data.AttCategory === 'Color'}), color)

    let Price = getPriceValues(_.find(filterData, (data) => {return data.name === 'Price'}), price)

    let Gender = getGenderValues(_.find(filterData, (data) => {return data.name === 'Gender'}), _.find(serverFilter, (data) => {return data.AttCategory === 'Gender'}), tag, p_cat, gender)

    // return new Promise((resolve, reject) => {
    //   resolve(`age=${Age}&size=${Size}&color=${Color}&price=${Price}&gender=${Gender}`)
    // })
    // console.log(`Returned Query :: age=${Age}&size=${Size}&color=${Color}&price=${Price}&gender=${Gender}`)
    return (`age=${Age}&size=${Size}&color=${Color}&price=${Price}&gender=${Gender}`)
}

function getAgeValues(ageEntity, actualEntity, age)
{
  if(age)
  {
    return age
  }
  else{
    if(ageEntity)
    {
      // console.log('Yesss Age Entity')
        if(actualEntity && Object.keys(ageEntity.value).length === Object.keys(actualEntity.Filters).length)
        {
          // console.log('Why equal')
            return 'all'
        }
        else
        {
            return Object.keys(ageEntity.value).length > 0 ? (ageEntity.value).join(',') : 'null'
        }
    }
    else
    {
      return 'all'
    }
  }
}

function getSizeValues(sizeEntity, actualEntity, size)
{
  if(size)
  {
    return size
  }
  else{
    if(sizeEntity)
    {
        if(actualEntity && Object.keys(sizeEntity.value).length === Object.keys(actualEntity.Filters).length)
        {
            return 'all'
        }
        else
        {
            return Object.keys(sizeEntity.value).length > 0 ? (sizeEntity.value).join(',') : 'null'
        }
    }
    else
    {
      return 'all'
    }
  }
}

function getColorValues(colorEntity, actualEntity, color)
{
  if(color)
  {
    return color
  }
  else{
    if(colorEntity)
    {
        if(actualEntity && Object.keys(colorEntity.value).length === Object.keys(actualEntity.Filters).length)
        {
            return 'all'
        }
        else
        {
            return Object.keys(colorEntity.value).length > 0 ? (colorEntity.value).join(',') : 'null'
        }
    }
    else
    {
      return 'all'
    }
  }
}

function getPriceValues(priceEntity,price)
{
  if(price)
  {
    return `${price.split(',')[0]},${price.split(',')[1]}`
  }
  else
  {
    if(priceEntity)
    {
      console.log('Price :: ' + JSON.stringify(priceEntity));
      let obj = {
        min : priceEntity.value[0].min,
        max : priceEntity.value[0].max
      }

      return `${obj.min},${obj.max}`
    }
    else
    {
      return `${50},${50000}`
    }
  }
}

function getGenderValues(genderEntity, actualEntity, tag, p_cat, gender)
{
  // alert(tag + ' - ' + p_cat + ' - ' + gender)
  if(gender)
  {
    return gender
  }else{
  if(genderEntity)
    {
        if(actualEntity && Object.keys(genderEntity.value).length === Object.keys(actualEntity.Filters).length)
        {
            return 'all'
        }
        else
        {
            return Object.keys(genderEntity.value).length > 0 ? (genderEntity.value).join(',') : 'all'
        }
    }
    else
    {
      if(p_cat.toLowerCase() === 'baby clothes' || p_cat.toLowerCase() === 'kids clothes' || p_cat.toLowerCase() === 'footwear')
      {
        if(tag.toLowerCase().includes('girl'))
        {
          return 'Girl'
        }
        else if(tag.toLowerCase().includes('boy'))
        {
          return 'Boy'
        }
        else if(tag.toLowerCase() === 'all')
        {
          return 'all'
        }
        else
        {
          return 'all'
        }
      }
      else
      {
        return 'all'
      }
    }
  }
}