import * as Actions from "../actions";

const initialState = {
  data: [],
  shipping: null,
};

const shippingReducer = (state = initialState, action) => {
  switch (action.type) {
    case Actions.GET_SHIPPING: {
      return {
        ...state,
        shipping: action.payload,
      };
    }

    default:
      return state;
  }
};

export default shippingReducer;

