import { hostUrl } from "../config";
import jwtDecode from "jwt-decode";
import _ from "lodash";
import { showResponseMessage } from "./notification.actions";

// ... CONSTANTS ...

export const GET_PRODUCT = "[PRODUCT] GET PRODUCT";
export const GET_CATEGORY_FOR_PRODUCT = "[PRODUCT] GET CATEGORY FOR PRODUCT";
export const GET_SORTED_REVIEWS = "[PRODUCT REVIEW] GET SORTED REVIEWS";
export const GET_PRODUCT_REVIEW = "[PRODUCT REVIEW] GET PRODUCT REVIEW";
export const ADD_PRODUCT_REVIEW = "[PRODUCT REVIEW] ADD PRODUCT REVIEW";
export const GET_SIMILAR_PRODUCT = "[SIMILAR PRODUCT] GET SIMILAR PRODUCT";
export const GET_PRODUCT_SEO = "[PRODUCT] GET PRODUCT SEO";

// ... ACTIONS ...

export function getProduct(sku) {
  // console.log('SKU :: ' + sku);
  let query = `${hostUrl}/api/customer/product/bySlug/${sku}`;

  let request = fetch(query);

  return (dispatch) => {
    request
      .then((response) => response.json())
      .then((data) => {
        if (data.status === true) {
          // console.log('Data :: ' + JSON.stringify(data.data[0]));

          dispatch(getSimilarProduct(data.data[0].Id));
          dispatch(getProductCategory(data.data[0].Id));
          dispatch({
            type: GET_PRODUCT,
            payload: data.data[0],
          });
        } else {
          // console.log('Error :: ' + data.msg);
        }
      })
      .catch((err) => {
        // console.log('Server Error :: ' + err);
      });
  };
}

export function getProductReview(id) {
  let query = `${hostUrl}/api/customer/product-review/${id}`;

  return (dispatch) => {
    fetch(query)
      .then((response) => response.json())
      .then((data) => {
        console.log(data);
        if (data.status === true) {
          // console.log('Success :: ' + data.msg);

          let avgRating = _.meanBy(data.data, (review) => {
            return parseInt(review.rating);
          });
          let reviewCount = 0;
          _.find(data.data, (d) => {
            if (d.review) {
              reviewCount += 1;
            }
          });
          console.log(
            data.data,
            avgRating,
            data.data.length,
            reviewCount,
            "reviews"
          );

          dispatch({
            type: GET_PRODUCT_REVIEW,
            payload: {
              data: data.data,
              avgRating: avgRating,
              totalRating: data.data.length,
              totalReview: reviewCount,
            },
          });
        } else {
          dispatch({
            type: GET_PRODUCT_REVIEW,
            payload: {
              data: null,
              avgRating: null,
              totalRating: null,
              totalReview: null,
            },
          });
          // console.log('Error :: ' + data.msg);
        }
      })
      .catch((err) => {
        console.log(err.response);
        // console.log('Server Error :: ' + err);
      });
  };
}

export function getProductCategory(id) {
  let query = `${hostUrl}/api/customer/product/get-category/${id}`;

  let request = fetch(query);

  return (dispatch) => {
    request
      .then((response) => response.json())
      .then((data) => {
        if (data.status === true) {
          dispatch({
            type: GET_CATEGORY_FOR_PRODUCT,
            payload: data.data,
          });
        } else {
          // console.log('Error :: ' + data.msg);
        }
      })
      .catch((err) => {
        // console.log('Server Error :: ' + err);
      });
  };
}

export function addProductReview(dataArray, pro_id) {
  let url = `${hostUrl}/api/customer/product-review/`;

  let request = fetch(url, {
    method: "POST",
    body: dataArray,
  });

  return (dispatch) => {
    request
      .then((response) => response.json())
      .then((data) => {
        if (data.status === true) {
          dispatch(showResponseMessage(data.msg, "success"));
          dispatch({
            type: ADD_PRODUCT_REVIEW,
          });
          dispatch(getProductReview(pro_id));
        } else {
          dispatch(showResponseMessage(data.msg, "danger"));
          dispatch({
            type: ADD_PRODUCT_REVIEW,
          });
        }
      })
      .catch((err) => {
        dispatch(showResponseMessage(err.msg, "danger"));
        dispatch({
          type: ADD_PRODUCT_REVIEW,
        });
      });
  };
}

