export function getProductSEO(slug) {
  let query = `${hostUrl}/api/customer/product/product_seo/${slug}`;

  return (dispatch) => {
    fetch(query)
      .then((response) => response.json())
      .then((result) => {
        // console.log('Product SEO :: ' + JSON.stringify(result))
        dispatch({
          type: GET_PRODUCT_SEO,
          payload: result.data,
        });
      })
      .catch((err) => {
        // console.log('Error :: ' + err)
      });
  };
}

export function sortReviews(sortType, reviews = []) {
  return (dispatch) => {
    let sortedReviews = [];
    switch (parseInt(sortType)) {
      case 0:
        // console.log('Its Newest')
        sortedReviews = _.orderBy(reviews, ["review_id"], ["desc"]);
        break;

      case 1:
        // console.log('Its Helpful')
        sortedReviews = _.orderBy(reviews, ["rating"], ["desc"]);
        break;

      case 2:
        // console.log('Its Oldest')
        sortedReviews = _.orderBy(reviews, ["review_id"], ["asc"]);
        break;

      default:
        return null;
    }

    dispatch({
      type: GET_SORTED_REVIEWS,
      payload: sortedReviews,
    });
  };
}
