import {hostUrl} from '../config';
import _ from 'lodash'

import {showResponseMessage} from './notification.actions'

// ... CONSTANTS ...
export const SEARCH = "[SEARCH] SEARCH";


// ... ACTIONS ...

export function search(query)
{
    let url = `${hostUrl}/api/customer/search/${query}`

    return (dispatch) => {
        fetch(url)
        .then(response => response.json())
        .then((data) => {
            if(data.status === true)
            {
                console.log('Success :: ' + data.msg);
                console.log('Searched Data Actions :: ' + JSON.stringify(data.data));
                dispatch(showResponseMessage(data.msg, 'success'))

                let colab = data.data.map(item => ({
                    label:item.Name,
                    value:item.Slug
                }))

                dispatch({
                    type: SEARCH,
                    payload: data.data
                })
            }
            else
            {
                dispatch(showResponseMessage(data.msg, 'danger'))
                console.log('Error :: ' + data.msg);
            }
        })
        .catch(err => {
            dispatch(showResponseMessage(err, 'danger'))
            console.log('Server error :: ' + err);
        })
    }
}