import * as Actions from "../actions";

const initialState = {
  product_review: null,
  avgRating: null,
  totalRating: null,
  totalReview: null,
};

const reviewReducer = (state = initialState, action) => {
  switch (action.type) {
    case Actions.GET_PRODUCT_REVIEW:
      console.log(action.payload);
      {
        return {
          ...state,
          product_review: action.payload.data,
          avgRating: action.payload.avgRating,
          totalRating: action.payload.totalRating,
          totalReview: action.payload.totalReview,
        };
      }

    default:
      return state;
  }
};

export default reviewReducer;
