import {
  productFoundInCache,
  productInCacheDetail,
  productFoundInCookies,
  getLocalStorageObject,
  getLocalStorageString,
  removeProductFromCookies,
} from "../../Utilities";

import { hostUrl } from "../config";
import _ from "lodash";

export const CART_UPDATE = "[CART] CART UPDATE";
export const CART_SAVE = "[CART] CART SAVE";
export const GET_MINI_CART = "[MINI CART] GET MINI CART";
export const REMOVE_CART = "[CART] REMOVE CART";
export const CART_SUMMARY = "[CART] CART SUMMARY";

export function updateCart() {
  let totalProductsInCache = Object.keys(getLocalStorageObject("procache"))
    .length;
  return (dispatch) => {
    // console.log('Success  :: ' + data.msg);
    dispatch({
      type: CART_UPDATE,
      payload: {
        itemCount: totalProductsInCache,
        cartItems: getLocalStorageObject("cachedProDetail"),
        procache: getLocalStorageObject("procache"),
      },
    });
  };
}

export function saveDataToCart(product, pro_qty) {
  // console.log('Product  :: ' + JSON.stringify(product))
  let parsedProcache = getLocalStorageObject("procache");
  let parsedcachedProDetail = getLocalStorageObject("cachedProDetail");

  if (
    productFoundInCache(product["Id"], parsedProcache) &&
    productInCacheDetail(product["Id"], parsedcachedProDetail)
  ) {
    let item = _.find(parsedProcache, (pro) => {
      if (pro[0] == product["Id"]) {
        pro[1] = pro_qty;
        return true;
      }
    });

    if (item) {
      // console.log('Found Item :: ' + item);
    } else {
      parsedProcache.push([product["Id"], pro_qty]);
      parsedcachedProDetail.push(product);
    }
  } else {
    // console.log('NOT Found');
    parsedProcache.push([product["Id"], pro_qty]);
    parsedcachedProDetail.push(product);
  }

  localStorage.setItem("procache", JSON.stringify(parsedProcache));
  localStorage.setItem(
    "cachedProDetail",
    JSON.stringify(parsedcachedProDetail)
  );

  return (dispatch) => {
    dispatch({
      type: CART_SAVE,
      payload: {
        cartItems: getLocalStorageObject("cachedProDetail"),
        procache: getLocalStorageObject("procache"),
        itemCount: Object.keys(getLocalStorageObject("procache")).length,
      },
    });
  };
}

export function getCartFromCache() {
  let itemsCount = Object.keys(getLocalStorageObject("procache")).length;
  let cartItems = getLocalStorageObject("cachedProDetail");
  let procache = getLocalStorageObject("procache");

  return (dispatch) => {
    dispatch({
      type: GET_MINI_CART,
      payload: {
        cartItems: cartItems,
        procache: procache,
        itemCount: itemsCount,
      },
    });
  };
}

export function removeCart() {
  localStorage.removeItem("procache");
  localStorage.removeItem("cachedProDetail");

  return (dispatch) => {
    dispatch({
      type: REMOVE_CART,
      payload: {
        cartItems: getLocalStorageObject("cachedProDetail"),
        procache: getLocalStorageObject("procache"),
        itemCount: 0,
      },
    });
  };
}

function findProductById(data, idToLookFor) {
  for (var i = 0; i < Object.keys(data).length; i++) {
    if (data[i].Id === parseInt(idToLookFor)) {
      return data[i];
    }
  }
}

export function getCartSummary(coupon, shipping) {
  console.log(coupon, "coupon");
  // const cookies = new Cookies();

  // console.log('cop details :: ' + JSON.stringify(coupon));
  // console.log('Shipping details :: ' + JSON.stringify(shipping));

  let procache =
    localStorage.getItem("procache") &&
    JSON.parse(localStorage.getItem("procache"));
  let data =
    localStorage.getItem("cachedProDetail") &&
    JSON.parse(localStorage.getItem("cachedProDetail"));
  // let cookie= cookies.get('shortlist')

  let summaryTemp = {
    totalValue: 0,
    discount: 0,
    shipping_charges: shipping ? shipping.shipping_charges : 0,
    shipping_amount: shipping ? shipping.amount : 0,
    remaining_amount: 0,
    estimated_gst: 0,
    final_payment: 0,
  };
  // console.log('Charges :: ' + summaryTemp.shipping_charges);

  procache &&
    procache.forEach((productCache) => {
      let product = findProductById(data, productCache[0]);
      if (product) {
        // JSON.stringify( "Found Product -> " + product);
        summaryTemp.totalValue += product.Price * productCache[1];

        if (product["Sale Price"] === 0) {
          // summaryTemp.discount += 0 ;
          if (
            coupon &&
            Object.keys(coupon).length > 0 &&
            _.find(product["is_coupon"].split(","), (cop) => {
              return cop === coupon.cop_code;
            })
          ) {
            if (coupon.cop_type === "Direct") {
              console.log("here it is");
              summaryTemp.discount = coupon.cop_discount;
              summaryTemp.final_payment +=
                product["Price"] * productCache[1] - coupon.cop_discount;
              console.log(summaryTemp);
            } else {
              let perc =
                ((product["Price"] * productCache[1]) / 100) *
                coupon.cop_discount;
              summaryTemp.discount = perc.toFixed(2);
              summaryTemp.final_payment +=
                product["Price"] * productCache[1] - perc;
            }
          } else {
            summaryTemp.final_payment += product.Price * productCache[1];
          }
        } else {
          if (
            coupon &&
            Object.keys(coupon).length > 0 &&
            _.find(product["is_coupon"].split(","), (cop) => {
              return cop === coupon.cop_code;
            })
          ) {
            if (coupon.cop_type === "Direct") {
              // alert('YEsssss')
              summaryTemp.discount +=
                (product.Price - product["Sale Price"]) * productCache[1] +
                coupon.cop_discount;
              summaryTemp.final_payment +=
                product["Sale Price"] * productCache[1] - coupon.cop_discount;
            } else {
              // alert('sdfdsf')
              summaryTemp.discount +=
                (product.Price - product["Sale Price"]) * productCache[1] +
                (coupon.cop_discount / 100) * product["Sale Price"];
              summaryTemp.final_payment +=
                product["Sale Price"] * productCache[1] -
                (coupon.cop_discount / 100) * product["Sale Price"];
            }
          } else {
            summaryTemp.discount +=
              (product.Price - product["Sale Price"]) * productCache[1];
            summaryTemp.final_payment +=
              product["Sale Price"] * productCache[1];
          }
        }
      }
    });

  summaryTemp.remaining_amount =
    summaryTemp.shipping_amount - summaryTemp.final_payment;

  if (shipping && summaryTemp.final_payment >= shipping.amount) {
    summaryTemp.shipping_charges = 0;
    summaryTemp.remaining_amount = 0;
  }

  // console.log('Final Payment :: ' + summaryTemp.final_payment)
  // console.log('Shipping Charges :: ' + summaryTemp.shipping_charges)

  if (shipping && summaryTemp.final_payment <= shipping.amount) {
    let isParticular,
      isCollective = false;

    // For Particular Product ...
    // console.log('IN SHIPPING')
    // console.log('Shipping details :: ' + JSON.stringify(shipping))
    if (shipping && shipping.particular_product) {
      procache &&
        procache.forEach((item) => {
          // console.log('Product :: ' + item[0])
          let findID = _.findIndex(
            shipping.particular_product.split(","),
            (pro) => {
              return item[0] === parseInt(pro);
            }
          );

          if (findID > -1) {
            summaryTemp.shipping_charges = 0;
            isParticular = true;
            // console.log('Particular true');
          }
        });
    }

    // if(shipping && shipping.particular_product)
    // {
    //     (shipping.particular_product.split(',')).map(pro => {
    //         let findID = procache && _.findIndex(procache, (item) => {console.log('Item :: '+ item[0] + "Pro ID :: "+pro); return item[0] === parseInt(pro)})

    //         if(findID > -1)
    //         {
    //             summaryTemp.shipping_charges = 0
    //             isParticular = true
    //             console.log('Particular true');
    //         }
    //     })
    // }

    // For Collective Product ...
    if (shipping && shipping.collective_product) {
      // console.log('Type :: ' + typeof shipping.collective_product)
      // let isShipping = false;
      shipping.collective_product.map((coll, key) => {
        // Collection iteration
        let proQty = 0;
        // console.log('Helllooo');
        procache &&
          procache.map((item) => {
            // Cart iteration

            coll.products.split(",").map((pro) => {
              //colection.product iteration

              // console.log('qty : ' + proQty);

              if (item[0] === parseInt(pro)) {
                proQty += item[1];
                // console.log('coll : ' + key);
                // console.log('proQty : ' + proQty);
                if (item[1] === coll.limit || proQty >= coll.limit) {
                  isCollective = true;
                  // console.log('Collection true');
                }
              }
            });
          });
      });
    }

    if (isParticular || isCollective) {
      summaryTemp.shipping_charges = 0;
      summaryTemp.remaining_amount = 0;
    } else if (procache && !Object.keys(procache).length > 0) {
      // alert('mjh me')
      summaryTemp.shipping_charges = shipping ? shipping.shipping_charges : 0;
    } else {
      // alert(summaryTemp.final_payment + ' - ' + summaryTemp.shipping_charges)

      summaryTemp.final_payment =
        parseInt(summaryTemp.final_payment) +
        parseInt(summaryTemp.shipping_charges);

      // alert(summaryTemp.final_payment)
    }
  }

  return (dispatch) => {
    dispatch(updateCart());
    dispatch({
      type: CART_SUMMARY,
      payload: { summary: summaryTemp, procache: procache, data: data },
    });
  };
}

