// ... CONSTANTS ...

export const GET_PROFILE = "[ACCOUNT] GET PROFILE";
export const UPDATE_PROFILE = '[ACCOUNT] UPDATE PROFILE'

export const GET_COUPONS = '[ACCOUNT] GET COUPONS'

export const UPDATE_CONTACT = '[ACCOUNT] UPDATE CONTACT'

export const UPDATE_ADDRESS = '[ACCOUNT] UPDATE ADDRESS'

export const UPDATE_PASSWORD = '[ACCOUNT] UPDATE PASSWORD'
export const UPDATE_PASSWORD_ERROR = '[ACCOUNT] UPDATE PASSWORD ERROR'

export const UPDATE_EMAIL_MARKET = '[ACCOUNT] UPDATE EMAIL MARKET'
export const UPDATE_SMS_MARKET = '[ACCOUNT] UPDATE SMS MARKET'

export const ADD_CHILD = '[ACCOUNT] ADD CHILD'
