export const DELETE_CHILD = '[ACCOUNT] DELETE CHILD'

export const RESEND_OTP = '[ACCOUNT] RESEND OTP'

// ... ACTIONS ...

export function getProfile()
{
    let id = null;
    if(localStorage.getItem('jwtToken'))
    {
        let obj = jwtDecode(localStorage.getItem('jwtToken'))
        id = obj.cust_id
    }

    let query = `${hostUrl}/api/customer/account/profile?cust_id=${id}`

    let request = fetch(query, {
        headers: {
            Authorization: 'Bearer ' + localStorage.getItem('jwtToken')
        }
    });

    return (dispatch) => {
        request
        .then(response => response.json())
        .then((data) => {
            if(data.status === true)
            {
                console.log('Success :: '+ data.msg);
                jwtService.set_MeToken(data.data)
                return dispatch({
                    type:GET_PROFILE,
                    payload: data.data
                })
            }
            else
            {
                return (console.log('Error :: ' + data.msg))
            }
        })
        .catch(err => {
            console.log('Server Error :: ' + err);
        })
    }
}

export function getCoupons()
{
    let id = null;
    if(localStorage.getItem('jwtToken'))
    {
        let obj = jwtDecode(localStorage.getItem('jwtToken'))
        id = obj.cust_id

        let query = `${hostUrl}/api/customer/coupons/getCashCoupons/${id}`

        let request = fetch(query, {
            headers: {
                Authorization: 'Bearer ' + localStorage.getItem('jwtToken')
            }
        });

        return (dispatch) => {
            request
            .then(response => response.json())
            .then((data) => {
                if(data.status === true)
                {
                    console.log('Success :: '+ data.msg);
                    dispatch(showResponseMessage(data.msg, 'success'))
                    return dispatch({
                        type:GET_COUPONS,
                        payload: data.data
                    })
                }
                else
                {
                    dispatch(showResponseMessage(data.msg, 'danger'))
                    return (console.log('Error :: ' + data.msg))
                }
            })
            .catch(err => {
                console.log('Server Error :: ' + err);
            })
        }
    }
    else
    {
        return dispatch => {
            dispatch(showResponseMessage('Sorry you are no logged in', 'danger'))
        }
    }
}

export function updateProfile(dataArray)
{
    console.log('Body :: ' + dataArray.get('cust_id'));

    let query = `${hostUrl}/api/customer/account/updateProfile`

    let request = fetch(query, {
        method: "POST",
        headers:{
        Authorization: 'Bearer ' + localStorage.getItem('jwtToken')
        },
        body: dataArray
    });

    return (dispatch) => {
        request
        .then(response => response.json())
        .then((data) => {
            if(data.status === true)
            {
                dispatch(getProfile())
                console.log('Success :: ' + data.msg);
                dispatch({
                    type : UPDATE_PROFILE,
                    payload: {Profilemsg: data.msg, variant:'green'}
                })
            }
            else
            {
                return dispatch({
                    type : UPDATE_PROFILE,
                    payload: {Profilemsg: data.msg, variant:'red'}
                })
            }
        })
        .catch(err =>  {
            console.log('Server Error :: ' + err);
        })
    }
}

export function ResendOTP(dataArray, isGeneral = null, cust_id = -1) {

    let url = isGeneral ? 
        `${hostUrl}/api/customer/signup/resend-otp?cust_id=${cust_id}`
        :
        `${hostUrl}/api/customer/signup/resend-otp`

    let request = fetch(url, {
        method: 'POST',
        headers: {
            Authorization: 'Bearer ' + localStorage.getItem('jwtToken')
        },
        body: dataArray
    })

    return dispatch => {
        request
        .then(response => response.json())
        .then(data => {
            if(data.status === true)
            {
                dispatch(showResponseMessage(data.msg,'success'))
                setTimeout(() => dispatch(showOTP()), 3000)

                dispatch({
                    type: RESEND_OTP,
                })
            }
            else 
            {
                dispatch(showResponseMessage(data.msg,'danger'))

                dispatch({
                    type: RESEND_OTP,
                })
            }
        })
        .catch(err => {
            dispatch(showResponseMessage(err.msg,'danger'))

            dispatch({
                type: RESEND_OTP,
            })
        })
    }
}

