export function updatePassword(crntPass, newPass, confrmPass)
{

    return (dispatch) => {

        if(newPass !== confrmPass)
        {
            dispatch({
                type : UPDATE_PASSWORD,
                payload: {Pswdmsg: 'New Password and Confirm Password should be same.', 
                            variant:'red'}
            })
        }
        else
        {
            let query = `${hostUrl}/api/customer/account/updatePassword`

            let formBody = new FormData();
            
            if(localStorage.getItem('jwtToken'))
            {
                let cust_id = (jwtDecode(localStorage.getItem('jwtToken'))).cust_id;
                formBody.append('cust_id',cust_id)
            }

            formBody.append('current_pass', crntPass);
            formBody.append('new_pass', confrmPass);

            let request = fetch(query, {
                method: "POST",
                headers:{
                Authorization: 'Bearer ' + localStorage.getItem('jwtToken')
                },
                body: formBody
            });

            request
            .then(response => response.json())
            .then((data) => {
                if(data.status === true)
                {
                    console.log('Success :: ' + data.msg);
                    dispatch({
                        type : UPDATE_PASSWORD,
                        payload: {Pswdmsg: data.msg, variant:'green'}
                    })
                    dispatch(getProfile())
                }
                else
                {
                    return dispatch({
                        type : UPDATE_PASSWORD_ERROR,
                        payload: {Pswdmsg: data.msg, variant:'red'}
                    })
                }
            })
            .catch(err =>  {
                console.log('Server Error :: ' + err);
            })

        }
    }

    

    return (dispatch) => {
        
    }
}

