export function addChild(dataArray)
{
    let query = `${hostUrl}/api/customer/account/addChild`

    let request = fetch(query, {
        method:'POST',
        headers: {
            Authorization: 'Bearer ' + localStorage.getItem('jwtToken')
        },
        body: dataArray
    })

    return (dispatch) => {
        request
        .then(response => response.json())
        .then((data) => {
            if(data.status === true)
            {
                dispatch(getProfile())
                dispatch({
                    type: ADD_CHILD,
                    payload: {msg:data.msg, variant:'green'}
                })
            }
            else
            {
                dispatch({
                    type: ADD_CHILD,
                    payload: {msg:data.msg, variant:'red'}
                })
            }
        })
        .catch(err => {
            dispatch({
                type: ADD_CHILD,
                payload: {msg:'Server Error :: '+ err, variant:'red'}
            })
        })
    }
}

