import {hostUrl} from '../config'

// ... CONSTANTS ...

export const GET_SHIPPING  = `[SHIPPING] GET SHIPPING`

// ... ACTIONS ...

export function getShipping(){
    let URL = `${hostUrl}/api/customer/shipping`

    return (dispatch) => {
        fetch(URL)
        .then(response => response.json())
        .then((data) => {
            dispatch({
                type: GET_SHIPPING,
                payload: data.data
            })
        })
        .catch(err => {
            dispatch({
                type: GET_SHIPPING,
                payload: null
            })
        })
    }
}