// ... CONSTANTS ...

export const GET_POPULATED_FILTER = "[FILTER] GET POPULATED FILTER";
export const GET_FILTER_COLLECTION = "[FILTER] GET FILTER COLLECTION";

// ... ACTIONS ...

export function populateServerFilters(serverFilter, filterQuery)
{
    let selectedFilter = []
    console.log('query :: ' + filterQuery)
    filterQuery.split('&').map(filter => {
        let filterIndex = _.find(serverFilter, index => {return index.AttCategory && index.AttCategory.toUpperCase() === (filter.split('=')[0]).toUpperCase()})
        if(filterIndex)
        {
            // console.log('YESSSS :: ' + filter.split('=')[0].toUpperCase())
            selectedFilter.push(popFilters(filterIndex, filter))
        }
    })

    console.log('Server Filter Clone :: ' + JSON.stringify(selectedFilter));
    return (dispatch) => {
        dispatch({
            type: GET_POPULATED_FILTER,
            payload: selectedFilter
        })
    }
}

function popFilters(filterArray, filter)
{
    let obj = {}
    if(filter.split('=')[0] !== 'price' &&  filter.split('=')[1] === 'all')
    {
        obj = {
            name : filterArray.AttCategory,
            value : filterArray.Filters
        }
    }
    else if(filter.split('=')[0] !== 'price' && filter.split('=')[1] !== 'null')
    {       
        // console.log('Not All and not Null')
        let attValue = []
        filter.split('=')[1].split(',').map(val => {
            if(_.find(filterArray.Filters, found => {return found === val}))
            {
                // console.log('Value found...')
                attValue.push(val)
            }
            return attValue
        })

        obj = {
            name : filterArray.AttCategory,
            value : attValue
        }
    }
    return obj
}

export function filterCollection(filterArray, filter)
{

    console.log('filterArray :: ' + JSON.stringify(filterArray))
    console.log('filter :: ' + JSON.stringify(filter))
  let updatedArr = filterArray

  // return new Promise((resolve, reject) => {

      if(Object.keys(updatedArr).length > 0)
      {
        if(filter.name !== 'Price')
        {
          let catIndex = _.findIndex(updatedArr, (item) => {return item.name === filter.name})

          if(catIndex > -1)
          {
            let valueIndex = _.findIndex(updatedArr[catIndex].value, (val) => { return val === filter.value})
      
            if(valueIndex > -1)
            {
              (updatedArr[catIndex].value).splice(valueIndex,1)
            }
            else
            {
              (updatedArr[catIndex].value).push(filter.value)
            }
          }
          else
          {
            let obj = {
              name: filter.name,
              value: [filter.value]
            }
            updatedArr.push(obj);
          }
        }
        else
        {
          let priceIndex = _.findIndex(updatedArr, (item) => {return item.name === filter.name})

          if(priceIndex > -1)
          {
            updatedArr[priceIndex].value[0].min = filter.value.min
            updatedArr[priceIndex].value[0].max = filter.value.max
          }
          else
          {
            let obj = {
              name : filter.name,
              value: [filter.value]
            }

            updatedArr.push(obj)
          }
        }

      }
      else
      {
          let obj = {
              name: filter.name,
              value: [filter.value]
          }
          updatedArr.push(obj)
      }
      // resolve(updatedArr)
  // })

  console.log('After UPdated Arr :: ' + JSON.stringify(updatedArr))
  return dispatch => {
      dispatch({
          type : GET_FILTER_COLLECTION,
          payload: updatedArr
      })
  }
//   return updatedArr;
}