import * as Actions from '../actions'

const initialState = {
    data: null
}

const bannerReducer = (state = initialState, action) => {
    switch(action.type)
    {
        case Actions.GET_BANNER:
            {
                return {
                    ...state,
                    bannerData: action.payload
                }
            }
        default:
            return state
    }
}

export default bannerReducer;