
// ... CONSTANTS ...
export const SHOW_RESPONSE_MESSAGE = '[MESSAGE] SHOW MESSAGE'
export const HIDE_RESPONSE_MESSAGE = '[MESSAGE] HIDE MESSAGE'
export const SHOW_CART_RESPONSE_MESSAGE = '[MESSAGE] SHOW CART MESSAGE'
export const HIDE_CART_RESPONSE_MESSAGE = '[MESSAGE] HIDE CART MESSAGE'
export const SHOW_AUTH_RESPONSE_MESSAGE = '[MESSAGE] SHOW AUTH MESSAGE'
export const HIDE_AUTH_RESPONSE_MESSAGE = '[MESSAGE] HIDE AUTH MESSAGE'
export const SHOW_SIGNUP_RESPONSE_MESSAGE = '[MESSAGE] SHOW SIGNUP MESSAGE'
export const HIDE_SIGNUP_RESPONSE_MESSAGE = '[MESSAGE] HIDE SIGNUP MESSAGE'

// ... ACTIONS ...

export function showResponseMessage(msg, variant)
{
    return (dispatch) => {
        dispatch({
            type: SHOW_RESPONSE_MESSAGE,
            payload: {
                response_message: msg,
                variant: variant
            }
        })
    }
}

export function hideResponseMessage()
{
    return (dispatch) => {
        dispatch({
            type: HIDE_RESPONSE_MESSAGE,
            payload: {
                response_message: '',
                variant: ''
            }
        })
    }
}

