import * as Actions from "../actions";


const initialState = {
  data: null
};

const authReducer = function(state = initialState, action) {
  switch (action.type) {
    
    case Actions.GET_REGISTRATION_SOCIAL: {
      return {
        ...state,
        isAuthenticated: action.payload.isAuthenticated, 
        user_object: action.payload.user_object
      };
    }

    case Actions.GET_CART_FOR_CUSTOMER: {
      return {
        ...state, 
        itemCount: action.payload.itemCount, 
        productQuantities: action.payload.productQuantities, 
        productData: action.payload.productData
      }
    }

    case Actions.GET_LOGIN_SOCIAL: {
      return {
        ...state, 
        isAuthenticated: action.payload.isAuthenticated, 
        user_object: action.payload.user_object
      }
    }

    case Actions.GET_MANUAL_LOGIN: 
      {
        return {
          ...state,
          loggedIn: action.payload.loggedIn
        }
      }

    case Actions.LOGOUT:
      {
        return {
          ...state,
          reload: action.payload.reload,
          login_msg: null,
          variant:'black'
        }
      }

    case Actions.SIGNUP:
      {
        return {
          ...state,
          signup_msg: action.payload.signup_msg,
          variant: action.payload.variant
        }
      }

    case Actions.STATE_NULL:
      {
        return {
          ...state,
          login_msg: action.payload.login_msg,
          variant: action.payload.variant
        }
      }

    case Actions.LOGIN_SUCCESS:
      {
        return {
          ...state,
          user: action.payload.user,
          login_msg: action.payload.login_msg,
          variant: action.payload.variant,
          is_verified: null
        }
      }

    case Actions.LOGIN_ERROR:
      {
        return {
          ...state,
          user: null,
          login_msg: action.payload.login_msg,
          variant: action.payload.variant,
          is_verified: action.payload.is_verified
        }
      }

    case Actions.GET_RESEND_EMAIL:
      {
        return {
          ...state
        }
      }

    case Actions.GUEST_SIGNUP:
      {
        return {
          ...state,
          guest_variant: action.payload.guest_variant,
          guest_msg: action.payload.guest_msg
        }
      }

    case Actions.VERIFY_OTP:
      {
        return {
          ...state,
          otp_msg: action.payload.msg,
          otp_variant: action.payload.variant,
          guest_obj: action.payload.guest_obj
        }
      }

    case Actions.SHOW_OTP:
      {
        return {
          ...state,
          otpDialog: action.payload,
          guest_variant: action.payload.guest_variant,
          guest_msg: action.payload.guest_msg
        }
      }

    case Actions.CLOSE_OTP:
      {
        return {
          ...state,
          otpDialog: action.payload
        }
      }
    
    case Actions.SET_USER_DATA:
      {
        return {
          ...state,
          user: action.payload
        }
      }

    case Actions.REMOVE_USER_DATA:
      {
        return {
          ...state,
          user:action.payload.user
        }
      }

    default:
      return state;
  }
};

export default authReducer;
