import * as Actions from '../actions'

const initialState = {
    data: null,
    coupon_applied:false,
    order_re_order:[]
}

const orderReducer = (state = initialState, action) => {
    switch(action.type)
    {
        case Actions.PLACE_ORDER:
            {
                return {
                    ...state,
                    orderMessage: action.payload.orderMessage
                }
            }

        case Actions.COUPON_APPLY:
            {
                return{
                    ...state,
                    coupon_applied: action.payload.coupon_applied,
                    couponMsg: action.payload.couponMsg,
                    coupon_details: action.payload.coupon_details
                }
            }

        case Actions.COUPON_RESET:
            {
                return {
                    ...state,
                    coupon_applied: action.payload.coupon_applied,
                    couponMsg: action.payload.couponMsg,
                    coupon_details: action.payload.coupon_details
                }
            }

        case Actions.ORDER_SUMMARY:
            {
                return {
                    ...state,
                    order_summary: action.payload.data,
                    subTotal: action.payload.subTotal
                }
            }
        
        case Actions.UPDATE_ORDER_STATUS:
            {
                return {
                    ...state,
                }
            }

        case Actions.ORDER_HISTORY:
            {
                return {
                    ...state,
                    order_history: action.payload
                }
            }

        case Actions.TRACK_ORDER:
            {
                return {
                    ...state,
                    track_order: action.payload
                }
            }

        case Actions.ORDER_QUICK_REORDER:
            {
                return {
                    ...state,
                    order_re_order: action.payload
                }
            }
        
        case Actions.ORDER_DETAIL:
            {
                return {
                    ...state,
                    order_detail: action.payload
                }
            }

        default: 
            return state;
    }
}

export default orderReducer