export function placeOrder(coupon, cart_summary, payment_method) {
	console.log("PLACED")
  // coupon &&  alert('Coupon :: ' + coupon.cop_id)
  let order_detail = [];
  let id = jwtDecode(localStorage.getItem("_me")).cust_id;
  let cart = getLocalStorageObject("cachedProDetail");
  let procache = getLocalStorageObject("procache");

  let couponCount = 0;
  cart.map((item) => {
    procache.map((pro) => {
      let order = {};
      if (pro[0] === item.Id) {
        if (
          coupon &&
          _.find(item["is_coupon"].split(","), (cop) => {
            return cop === coupon.cop_code;
          })
        ) {
          // alert('YEss coupon...')
          couponCount++;
        }
        order = {
          itemSku: item["SKU"],
          itemCount: pro[1],
          subTotal: subTotallAmount(item, pro[1], coupon),
        };

        order_detail.push(order);
      }
    });
  });

  console.log("id :: " + id);
  console.log("order detail :: " + JSON.stringify(order_detail));

  // alert('Cart Summary :: ' + JSON.stringify(order_detail))

  let formBody = new FormData();
  formBody.append("payment_method", payment_method);
  if (coupon && Object.keys(coupon).legnth > 0) {
    formBody.append("coupon_id", coupon.cop_id);
  }
  formBody.append("cust_id", id);
  formBody.append("order_details", JSON.stringify(order_detail));
  coupon && couponCount > 0
    ? formBody.append("coupon_applied", coupon.cop_code)
    : formBody.append("coupon_applied", "");
  cart_summary && formBody.append("discount", cart_summary.discount);
  cart_summary &&
    formBody.append("shipping_charges", cart_summary.shipping_charges);
  formBody.append("source", 1);

  let query = `${hostUrl}/api/customer/order`;

  let request = fetch(query, {
    method: "POST",
    body: formBody,
  });
  return (dispatch) => {
    request
      .then((response) => response.json())
      .then((data) => {
		  console.log(data,"Order Details")
        if (data.status === true) {
          console.log("Success :: " + data.msg);
          if (localStorage.getItem("gtToken")) {
            localStorage.removeItem("gtToken");
            localStorage.removeItem("_me");

            // dispatch(removeCart())
            // dispatch(resetCoupon())
            // dispatch({
            //     type: PLACE_ORDER,
            //     payload: {
            //         orderMessage: data.msg
            //     }
            // })
          }
          // else
          // {
          //     dispatch(getOrderSummary(data.data.order_no))
          //     dispatch(removeCart())
          //     dispatch(resetCoupon())
          //     dispatch({
          //         type: PLACE_ORDER,
          //         payload: {
          //             orderMessage: data.msg
          //         }
          //     })
          // }

          dispatch(getOrderSummary(data.data.order_no));
          dispatch(removeCart());
          dispatch(resetCoupon());
          dispatch({
            type: PLACE_ORDER,
            payload: {
              orderMessage: data.msg,
            },
          });
        } else {
          dispatch({
            type: PLACE_ORDER,
            payload: {
              orderMessage: data.msg,
            },
          });
        }
      })
      .catch((err) => {
        console.log("Server Error :: " + err);
      });
  };
}

export function updateOrderStatus(order_id, status) {
  let id = -1;
  if (localStorage.getItem("jwtToken")) {
    let obj = jwtDecode(localStorage.getItem("jwtToken"));
    id = obj.cust_id;

    let formBody = new FormData();
    formBody.append("cust_id", id);
    formBody.append("order_id", order_id);
    formBody.append("status", status);

    let url = `${hostUrl}/api/customer/order/updateStatus`;

    let request = fetch(url, {
      method: "POST",
      headers: {
        Authorization: "Bearer " + localStorage.getItem("jwtToken"),
      },
      body: formBody,
    });

    return (dispatch) => {
      request
        .then((response) => response.json())
        .then((data) => {
          if (data.status === true) {
            dispatch(getOrderDetail(order_id));
            dispatch({
              type: UPDATE_ORDER_STATUS,
            });
          }
        });
    };
  } else {
    return (dispatch) => {
      dispatch(showResponseMessage("Sorry you are no logged in", "danger"));
    };
  }
}

const subTotallAmount = (pro, qty, coupon) => {
  let total = pro["Sale Price"]
    ? Math.round(qty * pro["Sale Price"])
    : Math.round(qty * pro.Price);

  if (
    coupon &&
    _.find(pro["is_coupon"].split(","), (cop) => {
      return cop === coupon.cop_code;
    })
  ) {
    //   console.log('Coupon applied on :: ' + this.props.pro['Name']);
    coupon.cop_type === "Direct"
      ? (total = total - coupon.cop_discount)
      : (total = total - (coupon.cop_discount / 100) * total);
  }

  console.log("sub Total :: " + total);
  return total;
};

export function getOrderSummary(orderID) {
  let url = `${hostUrl}/api/customer/order/summary/${orderID}`;

  return (dispatch) => {
    fetch(url)
      .then((response) => response.json())
      .then((data) => {
        if (data.status === true) {
          console.log("Success :: " + data.msg);
          console.log("Order Summary :: " + JSON.stringify(data.data));

          let subTotal = 0;
          data.data.products.map((pro) => {
            subTotal += pro.total_price;
          });
          // let subTotal = _.add(products.map(pro => {return pro.total_price}))
          console.log("subTotal :: " + subTotal);
          dispatch({
            type: ORDER_SUMMARY,
            payload: { data: data.data, subTotal: subTotal },
          });
        } else {
          console.log("Error :: " + data.msg);
        }
      })
      .catch((err) => {
        console.log("Server Error :: " + err);
      });
  };
}

export function getOrderDetail(id) {
  let url = `${hostUrl}/api/customer/order/detail/${id}`;

  let request = fetch(url, {
    headers: {
      Authorization: "Bearer " + localStorage.getItem("jwtToken"),
    },
  });

  return (dispatch) => {
    request
      .then((response) => response.json())
      .then((data) => {
        if (data.status === true) {
          dispatch({
            type: ORDER_DETAIL,
            payload: data.data,
          });
        } else {
          dispatch(showResponseMessage(data.msg, "danger"));
        }
      });
  };
}

export function getOrderHistory(status = -1, date = "null") {
  let id = null;
  if (localStorage.getItem("jwtToken")) {
    let obj = jwtDecode(localStorage.getItem("jwtToken"));
    id = obj.cust_id;

    let url = ``;

    url =
      parseInt(status) === -1 && date === "null"
        ? `${hostUrl}/api/customer/order/history/${id}?date=all`
        : parseInt(status) > -1 && date === "null"
        ? `${hostUrl}/api/customer/order/history/${id}?status=${status}`
        : parseInt(status) === -1 && date !== "null"
        ? `${hostUrl}/api/customer/order/history/${id}?date=${date}`
        : `${hostUrl}/api/customer/order/history/${id}?status=${status}&date=${date}`;

    // let query = parseInt(status) === -1 ? `${hostUrl}/api/customer/order/history/${id}`
    // : `${hostUrl}/api/customer/order/history/${id}?status=${status}`

    let request = fetch(url, {
      headers: {
        Authorization: "Bearer " + localStorage.getItem("jwtToken"),
      },
    });

    return (dispatch) => {
      request
        .then((response) => response.json())
        .then((data) => {
          if (data.status === true) {
            dispatch({
              type: ORDER_HISTORY,
              payload: data.data,
            });
          } else {
            dispatch(showResponseMessage(data.msg, "danger"));
            dispatch({
              type: ORDER_HISTORY,
              payload: [],
            });
          }
        });
    };
  } else {
    return (dispatch) => {
      dispatch(showResponseMessage("Sorry you are no logged in", "danger"));
    };
  }
}

export function getTrackOrder(order_no) {
  let URL = `${hostUrl}/api/customer/order/track_order/${order_no}`;

  return (dispatch) => {
    fetch(URL)
      .then((response) => response.json())
      .then((data) => {
        if (data.status === true) {
          dispatch({
            type: TRACK_ORDER,
            payload: data.data,
          });
        } else {
          dispatch(showResponseMessage(data.msg, "danger"));
          dispatch({
            type: TRACK_ORDER,
            payload: [],
          });
        }
      });
  };
}

