export function getQuickReorder(date = "null") {
  let id = null;
  if (localStorage.getItem("jwtToken")) {
    let cust = jwtDecode(localStorage.getItem("jwtToken"));
    id = cust.cust_id;

    let url = ``;

    url =
      date === "null"
        ? `${hostUrl}/api/customer/order/quick_reorder/${id}?date=all`
        : `${hostUrl}/api/customer/order/quick_reorder/${id}?date=${date}`;

    let request = fetch(url, {
      headers: {
        Authorization: "Bearer " + localStorage.getItem("jwtToken"),
      },
    });

    return (dispatch) => {
      request
        .then((response) => response.json())
        .then((data) => {
          if (data.status === true) {
            dispatch({
              type: ORDER_QUICK_REORDER,
              payload: data.data,
            });
          } else {
            dispatch(showResponseMessage(data.msg, "danger"));
            dispatch({
              type: ORDER_QUICK_REORDER,
              payload: [],
            });
          }
        });
    };
  } else {
    return (dispatch) => {
      dispatch(showResponseMessage("Sorry you are not logged in ", "danger"));
    };
  }
}

export function applyCoupon(coupon) {
  if (localStorage.getItem("jwtToken")) {
    let obj = jwtDecode(localStorage.getItem("jwtToken"));
    let id = obj.cust_id;

    let url = `${hostUrl}/api/customer/coupons/${coupon}/${id}`;

    return (dispatch) => {
      fetch(url)
        .then((response) => response.json())
        .then((data) => {
          if (data.status === true) {
            console.log("Success :: " + data.msg);
            console.log("Data of Coupon :: " + JSON.stringify(data.data));

            dispatch(getCartSummary(data.data, data.shipping));

            dispatch({
              type: COUPON_APPLY,
              payload: {
                coupon_details: data.data,
                coupon_applied: true,
                couponMsg: data.msg,
              },
            });
          } else {
            console.log("Error :: " + data.msg);
            dispatch({
              type: COUPON_APPLY,
              payload: {
                coupon_details: null,
                coupon_applied: false,
                couponMsg: data.msg,
              },
            });
          }
        })
        .catch((err) => {
          console.log("Server Error :: " + err);
        });
    };
  } else if (localStorage.getItem("gtToken")) {
    let obj = jwtDecode(localStorage.getItem("gtToken"));
    let id = obj.cust_id;

    let url = `${hostUrl}/api/customer/coupons/${coupon}/${id}`;

    return (dispatch) => {
      fetch(url)
        .then((response) => response.json())
        .then((data) => {
          if (data.status === true) {
            console.log("Success :: " + data.msg);
            console.log("Data of Coupon :: " + JSON.stringify(data.data));

            dispatch(getCartSummary(data.data, data.shipping));

            dispatch({
              type: COUPON_APPLY,
              payload: {
                coupon_details: data.data,
                coupon_applied: true,
                couponMsg: data.msg,
              },
            });
          } else {
            console.log("Error :: " + data.msg);
            dispatch({
              type: COUPON_APPLY,
              payload: {
                coupon_details: null,
                coupon_applied: false,
                couponMsg: data.msg,
              },
            });
          }
        })
        .catch((err) => {
          console.log("Server Error :: " + err);
        });
    };
  }
}

export function resetCoupon() {
  return (dispatch) => {
    dispatch({
      type: COUPON_RESET,
      payload: {
        coupon_details: "",
        coupon_applied: false,
        couponMsg: "",
      },
    });
  };
}
