import * as Actions from '../actions'

const initialState = {
    data: null,
    selectedFilter : {}
}

const filterReducer = (state = initialState, action) => {
    switch(action.type)
    {
        case Actions.GET_POPULATED_FILTER:
            {
                return {
                    ...state,
                    selectedFilter: action.payload
                }
            }

        case Actions.GET_FILTER_COLLECTION:
            {
                return {
                    ...state,
                    selectedFilter: action.payload
                }
            }

        default:
            return state
    }
}

export default filterReducer